import React from 'react';
import KofCompressibility from "./kofCompressibility";
import {connect} from "react-redux";

let mapStateToProps = (state)=> {
    return {
        kofCompresFirst:state.resFirstProb.kofCompressibilityFirst,
        kofCompresSecond:state.resSecondProb.kofCompressibilitySecond
    }
}
let mapDispatchToProps = () => {
    return {

    }
}
const KofCompressibilityComponent = connect(mapStateToProps, mapDispatchToProps)(KofCompressibility);
export default KofCompressibilityComponent;