import {combineReducers, createStore} from "redux";
import sourseDataReducer from "./sourseDataReducer";
import resultReducer from "./resultReducer";
import resultDataFirstProbeReducer from "./resultFirstProbeReducer";
import resultDataSecondProbeReducer from "./resultSecondProbeReducer";


let reducer = combineReducers({
        sourceData: sourseDataReducer,
        resFirstProb: resultDataFirstProbeReducer,
        resSecondProb: resultDataSecondProbeReducer,
        result: resultReducer,
    }
)

let store = createStore(reducer);
window.store = store;
export default store