import React from 'react';
import ErrorRate from "./errorRate";
import {connect} from "react-redux";
import {addNewErrorRateAC, decrementErrorRateAC, incrementErrorRateAC} from "../../../redux/sourseDataReducer";

let mapStateToProps = (state) => {
    return {
        errorRate: state.sourceData.errorRate
    }
}
let mapDispatchToProps = (dispatch) => {
    return{
        incr:() => {
            dispatch(incrementErrorRateAC())
        },
        decrRate: () => {
            dispatch(decrementErrorRateAC())
        },
        adNewRate: (addRate)=> {
            dispatch(addNewErrorRateAC(addRate))
        }
    }
}
const ErrorRateComponent = connect (mapStateToProps, mapDispatchToProps)(ErrorRate)
export default ErrorRateComponent;