import React from 'react';
import Density from "./density";
import {connect} from "react-redux";
import {
    addNewDensityFirstProbValueAC, addNewDensitySecondProbValueAC,
    decrementDensityAC, decrementSecondDensityAC,
    incrementDensityAC,
    incrementSecondDensityAC
} from "../../../redux/sourseDataReducer";



let mapStateToProps = (state) => {
    return {
        densityFirst: state.sourceData.densityFirst,
        densitySecond: state.sourceData.densitySecond
    }
}

let mapDispatchToProps = (dispatch) => {
    return {
        incrementFirst: () => {
            dispatch(incrementDensityAC())
        },
        decrementFirst: () => {
            dispatch(decrementDensityAC())
        },
        incrementSecond: () => {
            dispatch(incrementSecondDensityAC())
        },
        decrementSecond: () => {
            dispatch(decrementSecondDensityAC())
        },
        addNewDensityValue: (densitVal, id) => {
            if (id.slice(2, 3) == 1) {
                dispatch(addNewDensityFirstProbValueAC(densitVal))
            } else if (id.slice(2, 3) == 2) {
                dispatch(addNewDensitySecondProbValueAC(densitVal))
            }
        }
    }
}
const DensityContainer = connect(mapStateToProps, mapDispatchToProps)(Density);

export default DensityContainer