import React from 'react';
import s from './densityCast.module.css'


const DensityCast = (props) => {
    return (
        <div>
            <tr className={s.density}>Плотность приведен., кг/м<sup><sup>3</sup></sup>
                <div className={s.row}>
                    <input className={s.result} value={props.densityCastFirst}/>
                </div>
                <div className={s.row2}>
                    <input className={s.result} value={props.densityCastSecond}/>
                </div>
            </tr>
        </div>
    )
}
export default DensityCast;