import React from 'react';
import s from './graduationOreom.module.css'


const GraduationOreom = (props) => {
    let changeStateInput20 = React.createRef();
    let changeStateInput15 = React.createRef();
    let changeStateInput15Part2 = React.createRef();
    let changeStateInput20Part2 = React.createRef();

    let check15 = (chekBox15) => {
        props.check15(chekBox15);
    }
    let check20 = (chekBox20) => {
        props.check20(chekBox20)
    }

     let check15Second = (chekBox15) => {
        props.check15First(chekBox15)
    }
    let check20Second = (chekBox20) => {
        props.check20Second(chekBox20)
    }


return (
    <div>
        <tr className={s.density}>Темп. град. ареом.,
            <sup><sup><sup>o</sup></sup></sup>С
            <div className={s.row}>
                <label>
                    <input ref={changeStateInput15}
                           type="checkBox" onChange={() => {
                        check15(changeStateInput15.current.checked)
                        changeStateInput20.current.checked = false
                    }}/>
                    <span>15</span>
                </label>
                <label className={s.check20}>
                    <input ref={changeStateInput20}
                           type="checkBox" onChange={() => {
                            check20(changeStateInput20.current.checked)
                        changeStateInput15.current.checked = false}} checked={true}/>
                    <span>20</span>
                </label>
            </div>
            <div className={s.row2}>
                <label>
                    <input ref={changeStateInput15Part2}
                           onChange={() => {
                               check15Second(changeStateInput15Part2.current.checked)
                               changeStateInput20Part2.current.checked = false;
                           }}
                           type="checkBox"/>
                    <span>15</span>
                </label>
                <label className={s.check20}>
                    <input ref={changeStateInput20Part2}
                           onChange={() => {
                               check20Second(changeStateInput20Part2.current.checked)
                               changeStateInput15Part2.current.checked = false;
                           }}
                           checked={true}
                           type="checkBox"
                    />
                    <span>20</span>
                </label>
            </div>
        </tr>
    </div>
)
}
export default GraduationOreom;