import React from 'react';
import s from './temperatureCasts.module.css'

const TemperatureCasts = (props) => {
    return (
        <div onChange={({target})=> {
            props.addNewTempCast(target.value)
        }}>
            <tr className={s.density}>Температура (привед.),
                <sup><sup><sup>o</sup></sup></sup>С
                <div className={s.row}>
                    <button onClick={() => {props.decrTemp()}}>-</button>
                    <input className={s.result}
                           value={props.tempCast}/>
                    <button onClick={() => {props.incrTemp()}}>+</button>
                </div>
            </tr>
        </div>
    )
}
export default TemperatureCasts;