import React from 'react';
import s from './pressureCasts.module.css'

const PressureCasts = (props) => {
    return (
        <div onChange={({target})=>{
            props.addNewPres(target.value)
        }}>
            <tr className={s.density}>Давление (привед.), МПа
                <div className={s.row}>
                    <button onClick={()=>{props.decrPres()}}>-</button>
                    <input className={s.result}
                           value={props.pressureCast}/>
                    <button onClick={()=>{props.incrPres()}}>+</button>
                </div>
            </tr>
        </div>
    )
}
export default PressureCasts;