const INCREMENT_TEMPERATURE_CAST = 'INCREMENT-TEMPERATURE-CAST'
const DECREMENT_TEMPERATURE_CAST = 'DECREMENT-TEMPERATURE-CAST'
const ADD_NEW_TEMPERATURE_CAST = 'ADD-NEW-TEMPERATURE-CAST'
const INCREMENT_PRESSURE_CAST = 'INCREMENT-PRESSURE-CAST'
const DECREMENT_PRESSURE_CAST = 'DECREMENT-PRESSURE-CAST'
const ADD_NEW_PRESSURE_CAST = 'ADD-NEW-PRESSURE-CAST'
const INCREMENT_DENSITY_FIRST_PROB = 'INCREMENT-DENSITY-FIRST-PROB';
const DECREMENT_DENSITY_FIRST_PROB = 'DECREMENT-DENSITY-FIRST-PROB';
const ADD_NEW_DENSITY_FIRST_PROB_VALUE_ACTION_CREATOR = 'ADD-NEW-DENSITY-FIRST-PROB-VALUE-ACTION-CREATOR'
const INCREMENT_TEMPERATURE_FIRST_PROB = 'INCREMENT-TEMPERATURE-FIRST-PROB';
const DECREMENT_TEMPERATURE_FIRST_PROB = 'DECREMENT_TEMPERATURE_FIRST_PROB';
const ADD_TEMPERATURE_FIRST_PROB = 'ADD-TEMPERATURE-FIRST-PROB'
const TEMPERATURE_GRADUATION_OREOM_20 = 'TEMPERATURE-GRADUATION-OREOM-20';
const TEMPERATURE_GRADUATION_OREOM_15 = 'TEMPERATURE-GRADUATION-OREOM-15';
const INCREMENT_SECOND_DENSITY_PROB = 'INCREMENT-SECOND-DENSITY-PROB';
const DECREMENT_SECOND_DENSITY_PROB = 'DECREMENT-SECOND-DENSITY-PROB';
const ADD_NEW_DENSITY_SECOND_PROB_VALUE_ACTION_CREATOR = 'ADD-NEW-DENSITY-SECOND-PROB-VALUE-ACTION-CREATOR'
const INCREMENT_TEMPERATURE_SECOND_PROB = 'INCREMENT-TEMPERATURE-SECOND-PROB'
const DECREMENT_TEMPERATURE_SECOND_PROB = 'DECREMENT-TEMPERATURE-SECOND-PROB'
const ADD_NEW_TEMPERATURE= 'ADD-NEW-TEMPERATURE'
const TEMPERATURE_GRADUATION_SECOND_OREOM_15 = 'TEMPERATURE-GRADUATION-SECOND-OREOM-15'
const TEMPERATURE_GRADUATION_SECOND_OREOM_20 = 'TEMPERATURE-GRADUATION-SECOND-OREOM-20'
const INCREMENT_ERROR_RATE_ACTION_CREATOR = 'INCREMENT-ERROR-RATE-ACTION-CREATOR'
const DECREMENT_ERROR_RATE_ACTION_CREATOR = 'DECREMENT-ERROR-RATE-ACTION-CREATOR'
const ADD_NEW_ERROR_RATE_ACTION_CREATOR = 'ADD_NEW-ERROR-RATE-ACTION-CREATOR'

let initialState = {
    'densityFirst': '860.0',
    'temperatureFirst': '20',
    'graduationOreom': '20',
    'temperatureCast': '0',
    'pressureCast': '0.0',
    'densitySecond': '860.0',
    'temperatureSecond': '20',
    'graduationOreomSecond': '20',
   'errorRate': '0',

}

let sourseDataReducer = (state = initialState, action) => {
    switch (action.type) {
        case INCREMENT_DENSITY_FIRST_PROB:
            return {...state, densityFirst: '' + parseFloat((+state.densityFirst + 0.1).toFixed(2))};
        case DECREMENT_DENSITY_FIRST_PROB:
            return {...state, densityFirst: '' + parseFloat((+state.densityFirst - 0.1).toFixed(2))};
        case ADD_NEW_DENSITY_FIRST_PROB_VALUE_ACTION_CREATOR:
            return {...state,densityFirst:action.dens};
        case INCREMENT_TEMPERATURE_FIRST_PROB:
            return {...state, temperatureFirst:'' + parseFloat((+state.temperatureFirst + 0.1).toFixed(2))};
        case DECREMENT_TEMPERATURE_FIRST_PROB:
            return {...state, temperatureFirst:'' + parseFloat((+state.temperatureFirst - 0.1).toFixed(2))};
        case ADD_TEMPERATURE_FIRST_PROB:
            return {...state, temperatureFirst: action.temp};
        case TEMPERATURE_GRADUATION_OREOM_15:
            console.log('АкшенКриетор от нажатия на 15 градусов')
            return {...state, graduationOreom: '15'};
        case TEMPERATURE_GRADUATION_OREOM_20:
            console.log('АкшенКриетор от нажатия на 20 градусов')
            return {...state, graduationOreom: '20'};
        case INCREMENT_TEMPERATURE_CAST:
            return {...state,temperatureCast: '' + parseFloat((+state.temperatureCast + 0.1).toFixed(2))};
        case DECREMENT_TEMPERATURE_CAST:
            return {...state, temperatureCast: '' + parseFloat((+state.temperatureCast - 0.1).toFixed(2))};
        case ADD_NEW_TEMPERATURE_CAST:
            return {...state, temperatureCast: action.adNew};
        case INCREMENT_PRESSURE_CAST:
            return {...state, pressureCast: '' + parseFloat((+state.pressureCast + 0.1).toFixed(2))};
        case DECREMENT_PRESSURE_CAST:
            return {...state,pressureCast:'' + parseFloat((+state.pressureCast - 0.1).toFixed(2))};
        case ADD_NEW_PRESSURE_CAST:
            return {...state,pressureCast: action.adNew};
        case INCREMENT_SECOND_DENSITY_PROB:
            return {...state, densitySecond:''+parseFloat((+state.densitySecond+0.1).toFixed(2))};
        case DECREMENT_SECOND_DENSITY_PROB:
            return {...state,densitySecond:''+ parseFloat((+state.densitySecond-0.1).toFixed(2))};
        case ADD_NEW_DENSITY_SECOND_PROB_VALUE_ACTION_CREATOR:
            return {...state,densitySecond: action.dens};
        case INCREMENT_TEMPERATURE_SECOND_PROB:
            return {...state,temperatureSecond: ''+parseFloat((+state.temperatureSecond+0.1).toFixed(2))};
        case DECREMENT_TEMPERATURE_SECOND_PROB:
            return {...state,temperatureSecond: ''+parseFloat((+state.temperatureSecond-0.1).toFixed(2))};
        case ADD_NEW_TEMPERATURE:
            return {...state, temperatureSecond: action.temp};
        case TEMPERATURE_GRADUATION_SECOND_OREOM_15:
            console.log('АкшенКриетор от нажатия на 15 градусов вторая проба')
            return {...state, graduationOreomSecond: '15'};
        case TEMPERATURE_GRADUATION_SECOND_OREOM_20:
            console.log('АкшенКриетор от нажатия на 20 градусов вторая проба')
            return {...state, graduationOreomSecond: '20'};
        case INCREMENT_ERROR_RATE_ACTION_CREATOR:
            return {...state, errorRate: ''+parseFloat((+state.errorRate + 0.01).toFixed(2))};
        case DECREMENT_ERROR_RATE_ACTION_CREATOR:
            return {...state, errorRate: ''+parseFloat((+state.errorRate - 0.01).toFixed(2))};
        case ADD_NEW_ERROR_RATE_ACTION_CREATOR:
            return {...state, errorRate: action.value};
        default:
            return state;
    }
}

export default sourseDataReducer;
export const incrementTemperatureCastAC = () => ({type: INCREMENT_TEMPERATURE_CAST})
export const decrementTemperatureCastAC = () => ({type: DECREMENT_TEMPERATURE_CAST})
export const addNewTemperatureCastAC = (addTemp) => ({type: ADD_NEW_TEMPERATURE_CAST, adNew: addTemp})
export const incrementPressureCastAC = () => ({type: INCREMENT_PRESSURE_CAST})
export const decrementPressureCastAC = () => ({type: DECREMENT_PRESSURE_CAST})
export const addNewPressureCastAC = (addTemp) => ({type: ADD_NEW_PRESSURE_CAST, adNew: addTemp})
export const incrementDensityAC = () => ({type: INCREMENT_DENSITY_FIRST_PROB})
export const decrementDensityAC = () => ({type: DECREMENT_DENSITY_FIRST_PROB})
export const addNewDensityFirstProbValueAC = (densityVal) => ({
    type: ADD_NEW_DENSITY_FIRST_PROB_VALUE_ACTION_CREATOR,
    dens: densityVal
})
export const incrementTemperatureFirstAC = () => ({type: INCREMENT_TEMPERATURE_FIRST_PROB})
export const decrementTemperatureFirstAC = () => ({type: DECREMENT_TEMPERATURE_FIRST_PROB})
export const addNewTemperatureFirstAC = (addTemp) => ({type: ADD_TEMPERATURE_FIRST_PROB, temp: addTemp})
export const TemperatureGraduationOreom15AC = () => ({type: TEMPERATURE_GRADUATION_OREOM_15})
export const TemperatureGraduationOreom20AC = () => ({type: TEMPERATURE_GRADUATION_OREOM_20})
export const incrementSecondDensityAC = () => ({type: INCREMENT_SECOND_DENSITY_PROB});
export const decrementSecondDensityAC = () => ({type: DECREMENT_SECOND_DENSITY_PROB});
export const addNewDensitySecondProbValueAC = (densityVal) => ({type:ADD_NEW_DENSITY_SECOND_PROB_VALUE_ACTION_CREATOR, dens:densityVal})
export const incrementTemperatureSecondAC = () => ({type: INCREMENT_TEMPERATURE_SECOND_PROB});
export const decrementTemperatureSecondAC = () => ({type: DECREMENT_TEMPERATURE_SECOND_PROB});
export const addNewTemperatureSecondAC = (tempVal) => ({type:ADD_NEW_TEMPERATURE, temp:tempVal});
export const TemperatureGraduationOreom15SecondAC = () => ({type: TEMPERATURE_GRADUATION_SECOND_OREOM_15});
export const TemperatureGraduationOreom20SecondAC = () => ({type: TEMPERATURE_GRADUATION_SECOND_OREOM_20});
export const incrementErrorRateAC = () => ({type:INCREMENT_ERROR_RATE_ACTION_CREATOR});
export const decrementErrorRateAC = () => ({type:DECREMENT_ERROR_RATE_ACTION_CREATOR});
export const addNewErrorRateAC = (addRate) => ({type:ADD_NEW_ERROR_RATE_ACTION_CREATOR, value:addRate});

