import {link} from "../api/serviceConnect";
import {
    addNewDensity15SecondProbValueAC,
    updateCorrectGradK20K15Second, updateDensityCastSecond,
    updateDensitySecond, updateKofCompressibilitySecond,
    updateKofVolExpancionSecond
} from "../redux/resultSecondProbeReducer";
import {
    addNewDensity15FirstProbValueAC,
    updateCorrectGradK20K15, updateDensityCastFirst,
    updateDensityFirst, updateKofCompressibilityFirst,
    updateKofVolExpancionFirst
} from "../redux/resultFirstProbeReducer";
import store from "../redux/redux-store";
import {addCastDensityFinalAC} from "../redux/resultReducer";


export async function postToServer(state) {

    let dataCalculate = {
        'sourceFirstProbe': state.sourceData.densityFirst,
        'sourceSecondProbe': state.sourceData.densitySecond,
        'temperatureFirst': state.sourceData.temperatureFirst,
        'temperatureSecond': state.sourceData.temperatureSecond,
        'graduationOreom': state.sourceData.graduationOreom,
        'graduationOreomSecond': state.sourceData.graduationOreomSecond,
        'tempCast': state.sourceData.temperatureCast,
        'pressCast': state.sourceData.pressureCast,
        'errorRate': state.sourceData.errorRate
    }

    console.log(dataCalculate);
    const request = fetch(link + '/updateDataCalc', {
        method: 'POST',
        mode: 'cors',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({dataCalculate})
    })
    const response = await request;
    let comm = await response.json();

    store.dispatch(addNewDensity15FirstProbValueAC(comm.denc15First));
    store.dispatch(addNewDensity15SecondProbValueAC(comm.denc15Second))
    store.dispatch(updateCorrectGradK20K15(comm.KofK20_K15First));
    store.dispatch(updateCorrectGradK20K15Second(comm.KofK20_K15Second));
    store.dispatch(updateDensityFirst(comm.dens20First));
    store.dispatch(updateDensitySecond(comm.dens20Second));
    store.dispatch(updateKofVolExpancionFirst(comm.bettaFirst));
    store.dispatch(updateKofVolExpancionSecond(comm.bettaSecond));
    store.dispatch(updateKofCompressibilityFirst(comm.gammaFirst));
    store.dispatch(updateKofCompressibilitySecond(comm.gammaSecond));
    store.dispatch(updateDensityCastFirst(comm.castDensitytPCalcFirst));
    store.dispatch(updateDensityCastSecond(comm.castDensitytPCalcSecond));
    store.dispatch(addCastDensityFinalAC(comm.densityFinalCalc));

    console.log(comm);
}