import React from 'react';
import s from './densityCastErrorRate.module.css'

const DensityCastErrorRate = (props) => {
    return (
        <div>
            <tr className={s.density}>Плотность приведен., кг/м<sup><sup>3</sup></sup>
                <div className={s.row}>

                    <input className={s.result} value={props.densCastRate}/>

                </div>
            </tr>
        </div>
    )
}
export default DensityCastErrorRate;