import React from 'react';
import s from './density.module.css'

const Density = (props) => {

    return (
        <div onChange={
            ({target}) =>props.addNewDensityValue(target.value, target.id)}>
            <tr className={s.density}>Плотность (ареом.), кг/м<sup><sup>3</sup></sup>
                <div className={s.row}>
                    <button  onClick={()=>{props.decrementFirst()}}>-</button>
                    <input id='1-1'  className={s.result} value={props.densityFirst}/>
                    <button  onClick={()=>{props.incrementFirst()}} >+</button>
                </div>
                <div className={s.row}>
                    <button onClick={()=> {props.decrementSecond()}}>-</button>
                    <input id='1-2' className={s.result} value={props.densitySecond}/>
                    <button onClick={()=> {props.incrementSecond()}}>+</button>
                </div>
            </tr>
        </div>
    )
}
export default Density;