const UPDATE_CORRECT_GRAD_K_20_K_15_SECOND = 'UPDATE-CORRECT-GRAD-K-20-K-15-SECOND';
const UPDATE_DENSITY_SECOND = 'UPDATE-DENSITY-SECOND';
const UPDATE_KOF_VOL_EXPANCION_SECOND = 'UPDATE-KOF-VOL-EXPANCION-SECOND';
const UPDATE_KOF_COMPRESSIBILITY_SECOND = 'UPDATE-KOF-COMPRESSIBILITY-SECOND';
const UPDATE_DENSITY_CAST_SECOND = 'UPDATE-DENSITY-CAST-SECOND';
const ADD_NEW_DENSITY_15_SECOND_PROB_VALUE_ACTION_CREATOR = 'ADD-NEW-DENSITY-15-SECOND-PROB-VALUE-ACTION-CREATOR'

let initialState = {
    'densitySecond': '0',
    'correctGradK20K15Second': '0',
    'kofVolExpancionSecond': '0',
    'kofCompressibilitySecond': '0',
    'densityCastSecond': '0',
    'density15Second': '0.7',
}
const resultDataSecondProbeReducer = (state = initialState, action) => {
    switch (action.type) {
        case UPDATE_DENSITY_SECOND:
            return {...state, densitySecond: action.value};
        case UPDATE_CORRECT_GRAD_K_20_K_15_SECOND:
            return {...state, correctGradK20K15Second: action.value};
        case UPDATE_KOF_VOL_EXPANCION_SECOND:
            return {...state,kofVolExpancionSecond: action.value};
        case UPDATE_KOF_COMPRESSIBILITY_SECOND:
            return {...state,kofCompressibilitySecond: action.value};
        case UPDATE_DENSITY_CAST_SECOND:
            return {...state, densityCastSecond: action.value};
        case ADD_NEW_DENSITY_15_SECOND_PROB_VALUE_ACTION_CREATOR:
            return {...state, density15Second: action.dens};
        default:
            return state;
    }
}

export default resultDataSecondProbeReducer;

export const updateCorrectGradK20K15Second = (corrGrad) => ({type:UPDATE_CORRECT_GRAD_K_20_K_15_SECOND, value:corrGrad})
export const updateDensitySecond = (corrGrad) => ({type:UPDATE_DENSITY_SECOND, value:corrGrad})
export const updateKofVolExpancionSecond = (corrGrad) => ({type:UPDATE_KOF_VOL_EXPANCION_SECOND, value:corrGrad})
export const updateKofCompressibilitySecond= (corrGrad) => ({type:UPDATE_KOF_COMPRESSIBILITY_SECOND, value:corrGrad})
export const updateDensityCastSecond = (corrGrad) => ({type:UPDATE_DENSITY_CAST_SECOND, value:corrGrad})
export const addNewDensity15SecondProbValueAC=(densityVal) => ({type: ADD_NEW_DENSITY_15_SECOND_PROB_VALUE_ACTION_CREATOR, dens:densityVal})
