import React from 'react';
import s from './kofVolExpancion.module.css'


const KofVolExpancion = (props) => {
    return (
        <div>
            <tr className={s.density}>Коэф.объем.расш.,<sup><sup>о</sup></sup>C<sup><sup>-1</sup></sup>
                <div className={s.row}>
                    <input className={s.result} value={props.kofVolFirst}/>
                </div>
                <div className={s.row2}>
                    <input className={s.result} value={props.kofVolSecond}/>
                </div>
            </tr>
        </div>
    )
}
export default KofVolExpancion;