import React from 'react';
import s from './result.module.css'
import KofVolExpancion from "./kofVolExpancion/kofVolExpancion";
import KofCompressibility from "./kofCompressibility/kofCompressibility";
import ErrorRate from "./errorRate/errorRate";
import DensityCastErrorRate from "./densityCastErrorRate/densityCastErrorRate";
import {changeValues} from "../../util/changeValues";
import CorrectGradK20K15Component from "./correctGradK20K15/correctGradK20K15Component";
import Density20DegComponent from "./density20Deg/density20DegComponent";
import DensityCastComponent from "./densityCast/densityCastComponent";
import DensityCastErrorRateComponent from "./densityCastErrorRate/densityCastErrorRateComponent";
import ErrorRateComponent from "./errorRate/errorRateComponent";
import KofVolExpancionComponent from "./kofVolExpancion/kofVolExpancionComponent";
import KofCompressibilityComponent from "./kofCompressibility/kofCompressibilityComponent";
import Density15Container from "./density15/density15Container";
import DifferenceComponent from "../sourseData/difference/differenceComponent";


const Result = (props) => {


    return (
        <div onChange={({target})=>{changeValues(target, target.value)}}>
            <table className={s.sourseData}>Результат
                <Density15Container/>
                <DifferenceComponent/>
                <Density20DegComponent store = {props.store}/>
                <CorrectGradK20K15Component store={props.store}/>
                <KofVolExpancionComponent store={props.store}/>
                <KofCompressibilityComponent store={props.store}/>
                <DensityCastComponent store={props.store}/>
                <ErrorRateComponent store ={props.store}/>
                <DensityCastErrorRateComponent store={props.store}/>
            </table>

        </div>
    )
}
export default Result;