import React, {useEffect} from 'react';
import s from './sourceData.module.css'
import DensityContainer from "./density/densityContainer";
import TemperatureComponent from "./temperature/temperatureComponent";
import TemperatureCastsContainer from "./temperatureCasts/temperatureCastsContainer";
import PressureCastsComponent from "./pressureCasts/pressureCastsComponent";
import {changeValues} from "../../util/changeValues";
import GraduationOreomComponent from "./graduationOreom/graduationOreomComponent";
import {postToServer} from "../../util/dataToCalculate";


const SourceData = (props) => {
    console.log(props.sourceData+ 'courceData')
    useEffect(()=>{
        postToServer(props.sourceData);
    }, [props.sourceData.sourceData])
    return (
        <div onChange={({target}) => {
            changeValues(target, target.value);
           // postToServer(props.sourceData);
        }}>
            <table className={s.sourseData}>Исходные данные
                <div className={s.probs}>
                    <span className={s.first}>Первая проба</span>
                    <span>Вторая проба</span>
                </div>
                <DensityContainer/>
                <TemperatureComponent/>
                <GraduationOreomComponent/>
                <TemperatureCastsContainer/>
                <PressureCastsComponent/>
            </table>

        </div>
    )
}
export default SourceData;