const UPDATE_CORRECT_GRAD_K_20_K_15 = 'UPDATE-CORRECT-GRAD-K-20-K-15';
const UPDATE_DENSITY_FIRST = 'UPDATE-DENSITY-FIRST';
const UPDATE_KOF_VOL_EXPANCION_FIRST = 'UPDATE-KOF-VOL-EXPANCION-FIRST';
const UPDATE_KOF_COMPRESSIBILITY_FIRST = 'UPDATE-KOF-COMPRESSIBILITY-FIRST';
const UPDATE_DENSITY_CAST_FIRST = 'UPDATE-DENSITY-CAST-FIRST';
const INCREMENT_DENSITY15_FIRST_PROB = 'INCREMENT-DENSITY15-FIRST-PROB';
const DECREMENT_DENSITY15_FIRST_PROB = 'DECREMENT_DENSITY15_FIRST_PROB';
const ADD_NEW_DENSITY_15_FIRST_PROB_VALUE_ACTION_CREATOR = 'ADD-NEW-DENSITY-15-FIRST-PROB-VALUE-ACTION-CREATOR';

let initialState = {
    'densityFirst': '0',
    'correctGradK20K15First': '0',
    'kofVolExpancionFirst': '0',
    'kofCompressibilityFirst': '0',
    'densityCastFirst': '0',
    'density15First': '0.5',
}
const resultDataFirstProbeReducer = (state = initialState, action) => {
    switch (action.type) {
        case UPDATE_DENSITY_FIRST:
            return {...state, densityFirst: action.value};
        case UPDATE_CORRECT_GRAD_K_20_K_15:
            return {...state, correctGradK20K15First: action.value};
        case UPDATE_KOF_VOL_EXPANCION_FIRST:
            return {...state, kofVolExpancionFirst: action.value};
        case UPDATE_KOF_COMPRESSIBILITY_FIRST:
            return {...state, kofCompressibilityFirst: action.value};
        case UPDATE_DENSITY_CAST_FIRST:
            return {...state, densityCastFirst: action.value};
        case ADD_NEW_DENSITY_15_FIRST_PROB_VALUE_ACTION_CREATOR:
            return {...state, density15First: action.dens};
        default:
            return state;
    }
}

export default resultDataFirstProbeReducer;

export const updateCorrectGradK20K15 = (corrGrad) => ({type: UPDATE_CORRECT_GRAD_K_20_K_15, value: corrGrad})
export const updateDensityFirst = (corrGrad) => ({type: UPDATE_DENSITY_FIRST, value: corrGrad})
export const updateKofVolExpancionFirst = (corrGrad) => ({type: UPDATE_KOF_VOL_EXPANCION_FIRST, value: corrGrad})
export const updateKofCompressibilityFirst = (corrGrad) => ({type: UPDATE_KOF_COMPRESSIBILITY_FIRST, value: corrGrad})
export const updateDensityCastFirst = (corrGrad) => ({type: UPDATE_DENSITY_CAST_FIRST, value: corrGrad})
export const addNewDensity15FirstProbValueAC = (densityVal) => ({
    type: ADD_NEW_DENSITY_15_FIRST_PROB_VALUE_ACTION_CREATOR,
    dens: densityVal
})