import React from 'react';
import s from './density20Deg.module.css'


const Density20Deg = (props) => {
    return (
        <div>
            <tr className={s.density}>Плотность при 20<sup><sup>o</sup></sup>C, кг/м<sup><sup>3</sup></sup>
                <div className={s.row}>
                    <input className={s.result} value={props.dens20First}/>
                </div>
                <div className={s.row2}>
                    <input className={s.result} value={props.dens20Second}/>
                </div>
            </tr>
        </div>
    )
}
export default Density20Deg;