import React from 'react';
import s from './correctGradK20K15.module.css'


const CorrectGradK20K15 = (props) => {

    return (
        <div>
            <tr className={s.density}>Кор. град. К20/К15
                <div className={s.row}>
                    <input className={s.result} value={props.correctGradFirst}/>
                </div>
                <div className={s.row2}>
                    <input className={s.result} value={props.correctGradSec}/>
                </div>
            </tr>
        </div>
    )
}
export default CorrectGradK20K15;