import React from 'react';
import s from './errorRate.module.css'

const ErrorRate = (props) => {

    return (
        <div onChange={({target})=>props.adNewRate(target.value)}>
            <tr className={s.density}>Систематическая<br/>погрешность
                <div className={s.row}>
                    <button onClick={() => {props.decrRate()}}>-</button>
                    <input className={s.result} value={props.errorRate}/>
                    <button onClick={() => {props.incr()}}>+</button>
                </div>
            </tr>
        </div>
    )
}
export default ErrorRate;