
const ADD_CAST_DENSITY_FINAL = 'ADD-CAST-DENSITY-FINAL'

let initialState = {

    densityCastErrorRate: '0',
    densityRate: '0.0'
}

let resultDataReducer = (state = initialState, action) => {
    switch (action.type) {

        case ADD_CAST_DENSITY_FINAL:
            return {...state, densityCastErrorRate: action.value};
        default:
            return state;
    }
}

export default resultDataReducer;


export const addCastDensityFinalAC = (addDens) => ({type:ADD_CAST_DENSITY_FINAL, value:addDens});