import React from 'react';
import s from './temperature.module.css'
import {changeValues} from "../../../util/changeValues";

const Temperature = (props) => {
    return (
        <div onChange={({target}) =>{
            changeValues(target,target.value);
            props.addNewTemp(target.value, target.id)
        }}>
            <tr className={s.density}>Температура (ареом.),
                <sup><sup><sup>o</sup></sup></sup>С
                <div className={s.row}>
                    <button onClick={() => {props.decrTemp()}}>-</button>
                    <input id = '2-1' className={s.result} value={props.temperatureFirst}/>
                    <button onClick={() => {props.incrTemp()}}>+</button>
                </div>
                <div className={s.row2}>
                    <button onClick={() => {props.decrSecTemp()}}>-</button>
                    <input id = '2-2' className={s.result} value={props.temperatureSecond}/>
                    <button onClick={() => {props.incrSecTemp()}}>+</button>
                </div>
            </tr>
        </div>
    )
}
export default Temperature;