import React from 'react';
import s from './kofCompressibility.module.css'


const KofCompressibility = (props) => {
    return (
        <div>
            <tr className={s.density}>Коэф.сжимаемости,МПа<sup><sup>-1</sup></sup>
                <div className={s.row}>
                    <input className={s.result} value={props.kofCompresFirst}/>
                </div>
                <div className={s.row2}>
                    <input className={s.result} value={props.kofCompresSecond}/>
                </div>
            </tr>
        </div>
    )
}
export default KofCompressibility;