import React from 'react';
import {connect} from "react-redux";
import TemperatureCasts from "./temperatureCasts";
import {
    addNewTemperatureCastAC,
    decrementTemperatureCastAC,
    incrementTemperatureCastAC
} from "../../../redux/sourseDataReducer";


let mapStateToProps = (state) => {
    return {
        tempCast: state.sourceData.temperatureCast
    }
}
let mapDispatchToProps = (dispatch) =>{
    return {
        incrTemp:() => {
            dispatch(incrementTemperatureCastAC())
        },
        decrTemp:() =>{
            dispatch(decrementTemperatureCastAC())
        },
        addNewTempCast:(addTemp) => {
            dispatch(addNewTemperatureCastAC(addTemp))
        }
    }
}

const TemperatureCastsContainer = connect(mapStateToProps, mapDispatchToProps)(TemperatureCasts);
export default TemperatureCastsContainer;