import React from 'react';
import PressureCasts from "./pressureCasts";
import {connect} from "react-redux";
import {addNewPressureCastAC, decrementPressureCastAC, incrementPressureCastAC} from "../../../redux/sourseDataReducer";

let mapStateToProps = (state) => {
    return {
        pressureCast: state.sourceData.pressureCast
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        incrPres: () => {
            console.log('incr')
            dispatch(incrementPressureCastAC())
        },
        decrPres: () => {
            console.log('decr')
            dispatch(decrementPressureCastAC())
        },
        addNewPres: (addTemp) => {
            dispatch(addNewPressureCastAC(addTemp))
        }
    }
}
const PressureCastsComponent = connect(mapStateToProps, mapDispatchToProps)(PressureCasts)
export default PressureCastsComponent;