import React from 'react';
import s from './density15.module.css'
import {changeValues} from "../../../util/changeValues";

const Density15 = (props) => {

    let density15First = React.createRef();
    let density15Second = React.createRef();



    let changeDensityFirst = () => {
        let val = density15First.current.value;
        props.changeDensityFirst(val)
    }
    let changeDensitySec = () => {
        let val = density15Second.current.value;
        props.changeDensitySec(val);
    }

    return (
        <div onChange={({target}) => {
            props.changeDensityFirst(target.value)
        }
        }>
            <tr className={s.density}>Плотность при 15
                <sup><sup><sup>o</sup></sup></sup>C, кг/м<sup><sup>3</sup></sup>
                <div className={s.row}>
                    <input ref={density15First}
                           type='text'
                           className={s.result}
                           onChange={changeDensityFirst}
                           value={props.density15First}/>
                </div>
                <div className={s.row2}>
                    <input ref={density15Second}
                           type='text'
                           className={s.result}
                           onChange={changeDensitySec}
                           value={props.density15Second}/>
                </div>
            </tr>
        </div>
    )
}
export default Density15;