import React from 'react';

import {connect} from "react-redux";
import GraduationOreom from "./graduationOreom";
import {
    TemperatureGraduationOreom15AC,
    TemperatureGraduationOreom15SecondAC,
    TemperatureGraduationOreom20AC, TemperatureGraduationOreom20SecondAC
} from "../../../redux/sourseDataReducer";

let mapStateToProps = () => {
    return {}
}
let mapDispatchToProps = (dispatch) => {
    return {
        check15: (chekBox15) => {
            switch (chekBox15) {
                case true:
                    dispatch(TemperatureGraduationOreom15AC())
                    return console.log('Кнопка 15 нажата')
                case false:
                    return console.log('кнопка 15 не нажата')
            }
        },
        check20: (chekBox20) => {
            switch (chekBox20) {
                case true:
                    dispatch(TemperatureGraduationOreom20AC())
                    return console.log('Кнопка 20 нажата')
                case false:
                    return console.log('кнопка 20 не нажата')
            }
        },
        check15First: (chekBox15) => {
            switch (chekBox15) {
                case true:
                    dispatch(TemperatureGraduationOreom15SecondAC())
                    return console.log('Кнопка 15 нажата')
                case false:
                    return console.log('кнопка 15 не нажата')
            }
        },
        check20Second: (chekBox20) => {
            switch (chekBox20) {
                case true:
                    dispatch(TemperatureGraduationOreom20SecondAC())
                    return console.log('Кнопка 20 нажата')
                case false:
                    return console.log('кнопка 20 не нажата')
            }
        }
    }
}
const GraduationOreomComponent = connect(mapStateToProps,mapDispatchToProps)(GraduationOreom);
export default GraduationOreomComponent;