import React from 'react';
import {connect} from "react-redux";
import Temperature from "./temperature";
import {
    addNewTemperatureFirstAC, addNewTemperatureSecondAC,
    decrementTemperatureFirstAC, decrementTemperatureSecondAC,
    incrementTemperatureFirstAC, incrementTemperatureSecondAC
} from "../../../redux/sourseDataReducer";

let mapStateToProps = (state) => {
    return {
        temperatureFirst: state.sourceData.temperatureFirst,
        temperatureSecond: state.sourceData.temperatureSecond
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        incrTemp: () => {
            dispatch(incrementTemperatureFirstAC())
        },
        decrTemp: () => {
            dispatch(decrementTemperatureFirstAC())
        },
        incrSecTemp: () => {
            dispatch(incrementTemperatureSecondAC())
        },
        decrSecTemp: () => {
            dispatch(decrementTemperatureSecondAC())
        },
        addNewTemp: (value, id) => {
            if (id.slice(2, 3) == 1) {
                dispatch(addNewTemperatureFirstAC(value))
            } else if (id.slice(2, 3) == 2) {
                dispatch(addNewTemperatureSecondAC(value))
            }
        }
    }
}
const TemperatureComponent = connect(mapStateToProps, mapDispatchToProps)(Temperature);

export default TemperatureComponent;