import React from 'react';
import './difference.css'

const Difference = (props) => {

    let style, calc;

    let calculate = () => {
        let calcSec = props.density15Second;
        let calcFirst = props.density15First;
        calc = (Math.abs(calcSec - calcFirst)).toFixed(2);

        return calc
    }
    let styleDif = () => {
        if (calc > '0.7') {
            calc = "Расхождение >0,6 кг/м3 "
            style = 'resultDifRed';
        } else {
            style = 'resultDif'
        }
        return style;
    }

    return (
        <div className='mainDifference'>
            <table className='difference'>
                Расхождение между результатами
                приведения <br/>плотности двух частей
                пробы:
                <tr className={styleDif(calculate())}> {calc}</tr>
                <tr></tr>
            </table>
        </div>
    )
}
export default Difference;