import React from 'react';
import {connect} from "react-redux";
import Density15 from "./density15";
import {
    addNewDensity15FirstProbValueAC,

} from "../../../redux/resultFirstProbeReducer";
import {addNewDensity15SecondProbValueAC} from "../../../redux/resultSecondProbeReducer";

let mapStateToProps = (state) => {
    return {
        density15First: state.resFirstProb.density15First,
        density15Second: state.resSecondProb.density15Second
    }
}
let mapDispatchToProps = (dispatch) => {

    return {
        changeDensityFirst: (val) => {dispatch(addNewDensity15FirstProbValueAC(val))
        },
        changeDensitySec: (val) => {dispatch(addNewDensity15SecondProbValueAC(val))
        }
    }
}
const Density15Container = connect(mapStateToProps, mapDispatchToProps)(Density15);
export default Density15Container;